				"bodyHTML": "<p>From <a href=\"http://www.designinglife.com\">Jack Ricchiuto:</a></p>\n<ol>\n<li>Get to know the strengths and passions of people in your first and second circles.</li>\n<li>Make your strengths and passions more known to your 1st two circles.</li>\n<li>Discover who in your 1st two circles would benefit from introductions.</li>\n<li>Make high quality introductions at the best levels possible.</li>\n<li>Engage your 2nd circle to introduce you to people in your 3rd circle.</li>\n<li>Look for new opportunities to learn with and from your first two circles.</li>\n<li>Create new opportunities to collaborate with your 1st two circles.</li>\n<li>Increase your asset, positional, and generative value in your networks.</li>\n<li>Help other people increase their value in their networks.</li>\n<li>Built trust and help others build trust through promise making and keeping.</li>\n</ol>\n",
